body {
	background-color: #f4f8f9;
	font-size: 10px;
	margin: 9vh 25vh; }

.box {
 }	// background-color: #fff;

.box--shadow {
	-webkit-box-shadow: -1px 1px 2px rgba(50, 50, 93, 0.21), 1px 1px 2px rgba(50, 50, 93, 0.21);
	-moz-box-shadow: -1px 1px 2px rgba(50, 50, 93, 0.21), 1px 1px 2px rgba(50, 50, 93, 0.21);
	box-shadow: -1px 1px 2px rgba(50, 50, 93, 0.21), 1px 1px 2px rgba(50, 50, 93, 0.21); }

.header-menu {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	padding: 3vh 2vw; }

.btn {
	padding: 10px 20px; }

.btn--uppercase {
	text-transform: uppercase; }

.btn--capitalize {
	text-transform: capitalize; }

.btn--blue-inverted {
	border: 1px solid #2a7ee4;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	border-radius: 2px;
	color: #2a7ee4;
	font-weight: bold;
	font-size: 1em;
	padding: 6px 16px;
	text-transform: uppercase; }

.upload-file {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center; }

.btn--grey-inverted {
	border: 1px solid #d8e3ea;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	border-radius: 2px;
	color: #d8e3ea;
	padding: 6px 17px;
	text-transform: capitalize;
	font-size: 1em;
	background: transparent; }

.btn__icon {
	font-size: 2em; }

.btn__text {
	color: #98a1a8;
	display: block;
	font-size: 1.2em;
	font-weight: normal;
	float: right;
	line-height: 20px;
	padding-left: 12px;
	text-transform: capitalize; }


.input--hidden {
	display: none; }

.user {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	height: 92px; }

.photo {
	height: 22vh;
	margin: 0;
	overflow: hidden;
	position: relative;
	top: -10px;
	width: 22vh;
	margin-left: 1.3vw; }

.photo__img {
	left: -101px;
	position: relative;
	top: -25px;
	z-index: 9;
	-webkit-transform: scale(0.82);
	-moz-transform: scale(0.82);
	-o-transform: scale(0.82); }

.user-data {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	margin-left: 15px; }

.user-data__name {
	font-family: roboto;
	font-weight: normal;
	font-size: 1.7em;
	margin: 17px 0 5px; }

.user-data__description {
	font-size: 1.2em;
	font-family: roboto;
	margin: 5px 0; }

.user-data__icon {
	color: #c5d0d7;
	font-size: 1.3em;
	float: left; }

.user-data__description-text {
	padding: 10px; }

.user-nav {
	margin-top: 7px;
	position: relative;
	padding-left: 170px;
	border-top: 1px solid #e2e2e2; }

.user-nav ul {
	list-style: none;
	height: 50px;
	margin: 0;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	padding-left: 0; }

.user-nav ul li {
	line-height: 50px;
	text-transform: uppercase;
	font-family: roboto;
	font-size: 1.35em;
	color: #656a6f;
	padding: 0px 9px;
	cursor: pointer;
	height: 100%; }

.user-nav__link {
	text-decoration: none;
	color: #818689;
	position: relative;
	display: inline-block;
	line-height: 48px;
	overflow: hidden; }

.user-nav--active {
	border-bottom: 2px solid #308dfa; }

.user-nav__link.user-nav--active:before {
	content: '';
	position: absolute;
	bottom: 0px;
 }	// border: 1px solid #70befb;width: 100%;
